@import url(https://fonts.googleapis.com/css?family=Roboto:300);

.login-page {
    width: 360px;
    padding: 8% 0 0;
    margin: auto;
}

.form {
    position: relative;
    z-index: 1;
    background: #FFFFFF;
    max-width: 360px;
    margin: 0 auto 100px;
    padding: 45px;
    text-align: center;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
    -webkit-transform: translateY(+30%);
            transform: translateY(+30%);
}

.form input,select{
    font-family: "Roboto", sans-serif;
    outline: 0;
    background: #FFFFFF;
    border: 1px solid #4d4d4d;
    width: 100%;
    border-radius: 4px 4px 4px 4px;
    margin: 0 0 15px;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
    -webkit-transition-property: border;
    transition-property: border;
    -webkit-transition-duration: .4s;
            transition-duration: .4s;
}

.form input:focus{
    border: 1.5px solid #0000CD;
}
.formName{
    font-size: 36px;
    padding: 10px ;

}
.formInfo{
    font-size: 16px;
    padding: 20px ;

}
.form button {
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    outline: 0;
    background: #0000CD;
    width: 50%;
    border: 0;
    padding: 15px;
    color: #FFFFFF;
    border-radius: 4px 4px 4px 4px;
    font-size: 14px;
    -webkit-transition: all 0.3 ease;
    transition: all 0.3 ease;
    cursor: pointer;
}

.form button:hover, .form button:active, .form button:focus {
    background: #43A047;
}

.form .message {
    margin: 15px 0 0;
    color: #b3b3b3;
    font-size: 12px;
}

.form .message a {
    color: #4CAF50;
    text-decoration: none;
}

.form .register-form {
    display: none;
}

.container {
    position: relative;
    z-index: 1;
    max-width: 300px;
    margin: 0 auto;
}

.container:before, .container:after {
    content: "";
    display: block;
    clear: both;
}

.container .info {
    margin: 50px auto;
    text-align: center;
}

.container .info h1 {
    margin: 0 0 15px;
    padding: 0;
    font-size: 36px;
    font-weight: 300;
    color: #1a1a1a;
}

.container .info span {
    color: #4d4d4d;
    font-size: 12px;
}

.container .info span a {
    color: #000000;
    text-decoration: none;
}

.container .info span .fa {
    color: #EF3B3A;
}

body {
    background: #FFFFFF; /* fallback for old browsers */
    background: -webkit-gradient(linear, right top, left top, from(#FFFFFF), to(#f2f2f2));
    background: linear-gradient(to left, #FFFFFF, #f2f2f2);
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
